.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.About {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
  padding-left: 10%;
  padding-right: 10%;
}

.About-Title {
  font-size: 2em;
}

.About-Logo{

}

.About-Summary {
  text-align: center;
  font-size: 1.5em;
}

.About-Team {
  text-align: center;
  font-size: 1.5em;
}

.Team {
  display: flex;
  flex-direction: column;
  justify-content: center;
  background-color: #282c34;
  color: white;
  padding-left: 2%;
  padding-right: 2%;
}

.Team-MemberCard {
  display: flex;
  flex-direction: column;
}

/* Team Member Styles 
  Bold Names
  reduce margin between elements

*/
.Team-MemberName {
  font-size: 2em;
  font-weight: bold;
  margin-bottom: -1%;
}

.Team-MemberRole {
  font-size: 1em;
  margin-bottom: -1%;
  margin-top: -1%;
}

.Team-MemberImage {
  width: 100px;
  height: 100px;
  border-radius: 50%;
  margin-bottom: -1%;
}

.Team-MemberBio {
  font-size: 1em;
  margin-bottom: -1%;
  margin-top: -1%;
}

.Team-MemberLinkedin {
  font-size: 1em;
  margin-bottom: -1%;
  margin-top: -1%;
}

.Team-MemberOther {
  font-size: 1em;
  margin-bottom: -1%;
  margin-top: -1%;
}

.Team-MemberEmail {
  font-size: 1em;
  margin-bottom: -1%;
  margin-top: -1%;
}

.Team-MemberPhone {
  font-size: 1em;
  margin-bottom: -1%;
}

